import { useState,useEffect } from "react";



export default function useQuotes(){



  useEffect(() => {
    const storedQuotes = JSON.parse(localStorage.getItem("quotes"));
    if (storedQuotes && storedQuotes.length > 0) {
      setQuotes(storedQuotes);
    } else {
      const firstQuotes=[
        { id:0,text: "When you see a good move, look for a better one.", author: "Emanuel Lasker", comments: [] },
        { id:1, text: "The beauty of a move lies not in its appearance but in the thought behind it.", author: "Aaron Nimzowitsch", comments: [] }
      
      ];
      setQuotes(firstQuotes);
    }
    setPending(false);
  }, []);




      

const [quotes, setQuotes]=useState([]);
const [pending, setPending]= useState(true);
    const [error, setError]= useState(false);

const addComment=(quoteId, commentText)=>{
  console.log('Added Quote.')
  if(commentText=='')return;
  const updatedQuotes=quotes.map((q)=>{
    
    if(q.id==quoteId){
      const updatedComments = q.comments ? [...q.comments, commentText] : [commentText];
      return {...q,comments:updatedComments};}
    return q;
  });
  setQuotes(updatedQuotes);
    localStorage.setItem("quotes", JSON.stringify(updatedQuotes));
    console.log('New comment added successfuly!')
}


// const getData=()=>{
//   setPending(true);
//   const storedQuotes = JSON.parse(localStorage.getItem("quotes"));
//   if (storedQuotes && storedQuotes.length>0) {
//     setQuotes(storedQuotes);
//   } 
//   console.log(quotes);
//   setPending(false)
// }

const addQuote=(newQuote)=>{
    setPending(true);
    const updatedQuotes = quotes?[...quotes, {id:quotes.length, text:newQuote.text,author:newQuote.author,comments:[]}]:[{id:0, text:newQuote.text,author:newQuote.author,comments:[]}]
   
    setQuotes(updatedQuotes);
    localStorage.setItem("quotes", JSON.stringify(updatedQuotes));
   
    setPending(false);
    console.log('New quote added successfuly!')
}

return {quotes,pending, error, setData:addQuote,addComment};


}

